
































































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import intervalList from '@/constant/intervalList';
import recordTypeList from '@/constant/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
const api = dayjs();

@Component({
  components: {Tabs},
})
export default class Statistics extends Vue{
  tagString(tags: Tag[]){
    return tags.length === 0 ? '' : tags.map(t => t.name).join('');
  }
  beautify(string: string){
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now,'day')){
      return '';
    } else if (day.isSame(now.subtract(1,'day'),'day')) {   //isSame(now.valueOf() - 86400 * 1000,'day')
      return '';
    } else if (day.isSame(now,'year')) {
      return day.format('MD');
    } else {
      return day.format('YYYYMD');
    }
  }
  get recordList(){
    return (this.$store.state as RootState).recordList;
  }
  get groupedList(){
    const {recordList} = this;
    const newList = clone(recordList)
        .filter(r => r.type === this.type)
        .sort((a,b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {return [] as Result;}  //{return []}
    type Result = {title: string; total?: number; items: RecordItem[]}[];
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++ ){
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt),'day')){
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum,item) => {
        return sum + item.amount;
      },0)
    })
    return result;
  }
  beforeCreate(){
    this.$store.commit('fetchRecords');
  }
  type = '-';
  interval = 'day';
  intervalList = intervalList;
  recordTypeList = recordTypeList;
}
